.CardBox {
    padding: 15px;

    border: 1px solid #000;
    border-radius: 20px;
}
.CardBox:hover {
    transform: scale(1.1);
}


.ModalContainer {
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    z-index: 1;

    width: 100vw;
    height: 100vh;

    background-color: rgb(0, 0, 0, 0.5);

}
.Modal {
    background-color: red;

    display: flex;
    flex-direction: column;

    border-radius: 10px;

    width: 40%;
    padding: 10px;

}
.modal-header {
    display: flex;
    justify-content: end;
}

.modal-body-stats {
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.modal-body-stats-stat {
    display: flex;
    justify-content: center;
    border: 1px solid red;
}
.modal-body-stats-stat-name {
    text-transform: capitalize;
    padding: 10px;
}
.modal-body-stats-stat-value {
    padding: 10px;
}
.pokemon-name {
    cursor: pointer;
}
.card-pokemon-name {
    font-size: 20px;
    color: blue;
    text-transform: capitalize;
}



.cat-image {
    width: 50px;
    height: 50px;
}